<template lang="pug">
  div.row
    div.col-12
      div.page-header
        h1 Horizontal Timeline
      div(style="display:inline-block;width:100%;overflow-y:auto;")
        ul.timeline.timeline-horizontal
          li.timeline-item(v-for="event in events")
            div.timeline-badge(:class="getBadgeColorClass(event)")
              i.fa(:class="getEventIconClass(event)")
            div.timeline-panel
              div.timeline-heading
                h4.timeline-title.d-flex.align-items-center(v-if="event.title || event.imageSrc")
                  circular-image(style="display:inline-block",v-if="event.imageSrc",:img="{main_image_name: event.imageSrc}",:size="60")
                  span {{ event.title }}
                p(v-if="event.timestamp")
                  small.text-muted
                    i.fa.fa-clock-o
                    span  {{ getTimeFromNow(event.timestamp) }}
                    div(style="font-size:0.5em") {{ getFormattedDate(event.timestamp) }}
              div.timeline-body(v-if="event.body")
                p(:style="eventBodyStyle(event.body)") {{ event.body }}
</template>

<script>
  // Reference/credit
  // https://bootsnipp.com/snippets/a3BjR

  import moment from 'moment'
  import CircularImage from './CircularImage'
  import TimeHelpers from '../factories/TimeHelpers'

  export default {
    props: ['events'],
    methods: {
      getFormattedDate: TimeHelpers.getFormattedDate,
      getTimeFromNow: TimeHelpers.getTimeFromNow,

      eventBodyStyle(bodyString) {
        if (bodyString && bodyString.length >= 160)
          return {'font-size': '0.8em'}

        return {}
      },

      getBadgeColorClass(ev) {
        // primary, success, info, danger, warning, or nothing
        if (ev.badgeColor)
          return {[ev.badgeColor]: true}
        return {}
      },

      getEventIconClass(ev) {
        if (ev.icon)
          return {[ev.icon]: true}
        return {'fa-check': true}
      }
    },
    components: {
      'circular-image': CircularImage
    }
  }
</script>

<style scoped>
  /* Timeline */
  .timeline,
  .timeline-horizontal {
    list-style: none;
    padding: 20px;
    position: relative;
  }

  .timeline:before {
    top: 40px;
    bottom: 0;
    position: absolute;
    content: " ";
    width: 3px;
    background-color: #eeeeee;
    left: 50%;
    margin-left: -1.5px;
  }

  .timeline .timeline-item {
    margin-bottom: 20px;
    position: relative;
  }

  .timeline .timeline-item:before,
  .timeline .timeline-item:after {
    content: "";
    display: table;
  }

  .timeline .timeline-item:after {
    clear: both;
  }

  .timeline .timeline-item .timeline-badge {
    color: #fff;
    width: 54px;
    height: 54px;
    line-height: 52px;
    font-size: 22px;
    text-align: center;
    position: absolute;
    top: 18px;
    left: 50%;
    margin-left: -25px;
    background-color: #b72168;
    border: 3px solid #ffffff;
    z-index: 100;
    border-top-right-radius: 50%;
    border-top-left-radius: 50%;
    border-bottom-right-radius: 50%;
    border-bottom-left-radius: 50%;
  }

  .timeline .timeline-item .timeline-badge i,
  .timeline .timeline-item .timeline-badge .fa,
  .timeline .timeline-item .timeline-badge .glyphicon {
    top: 2px;
    left: 0px;
  }

  .timeline .timeline-item .timeline-badge.primary {
    background-color: #1f9eba;
  }

  .timeline .timeline-item .timeline-badge.info {
    background-color: #5bc0de;
  }

  .timeline .timeline-item .timeline-badge.success {
    background-color: #21b729;
  }

  .timeline .timeline-item .timeline-badge.warning {
    background-color: #b2b721;
  }

  .timeline .timeline-item .timeline-badge.danger {
    background-color: #ba1f1f;
  }

  .timeline .timeline-item .timeline-badge.dark {
    background-color: #740037;
  }

  .timeline .timeline-item .timeline-panel {
    position: relative;
    width: 46%;
    float: left;
    right: 16px;
    border: 1px solid #c0c0c0;
    background: #ffffff;
    border-radius: 2px;
    padding: 20px;
    -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
    box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
  }

  .timeline .timeline-item .timeline-panel:before {
    position: absolute;
    top: 26px;
    right: -16px;
    display: inline-block;
    border-top: 16px solid transparent;
    border-left: 16px solid #c0c0c0;
    border-right: 0 solid #c0c0c0;
    border-bottom: 16px solid transparent;
    content: " ";
  }

  .timeline .timeline-item .timeline-panel .timeline-title {
    margin-top: 0;
    color: inherit;
  }

  .timeline .timeline-item .timeline-panel .timeline-body > p,
  .timeline .timeline-item .timeline-panel .timeline-body > ul {
    margin-bottom: 0;
  }

  .timeline .timeline-item .timeline-panel .timeline-body > p + p {
    margin-top: 5px;
  }

  .timeline .timeline-item:last-child:nth-child(even) {
    float: right;
  }

  .timeline .timeline-item:nth-child(even) .timeline-panel {
    float: right;
    left: 16px;
  }

  .timeline .timeline-item:nth-child(even) .timeline-panel:before {
    border-left-width: 0;
    border-right-width: 14px;
    left: -14px;
    right: auto;
  }

  .timeline-horizontal {
    list-style: none;
    position: relative;
    padding: 20px 0px 20px 0px;
    display: inline-block;
  }

  .timeline-horizontal:before {
    height: 3px;
    top: auto;
    bottom: 26px;
    left: 56px;
    right: 0;
    width: 100%;
    margin-bottom: 20px;
  }

  .timeline-horizontal .timeline-item {
    display: table-cell;
    height: 340px;
    width: 20%;
    min-width: 320px;
    float: none !important;
    padding-left: 0px;
    padding-right: 20px;
    margin: 0 auto;
    vertical-align: bottom;
  }

  .timeline-horizontal .timeline-item .timeline-panel {
    top: auto;
    bottom: 64px;
    display: inline-block;
    float: none !important;
    left: 0 !important;
    right: 0 !important;
    width: 100%;
    margin-bottom: 20px;
  }

  .timeline-horizontal .timeline-item .timeline-panel:before {
    top: auto;
    bottom: -16px;
    left: 28px !important;
    right: auto;
    border-right: 16px solid transparent !important;
    border-top: 16px solid #c0c0c0 !important;
    border-bottom: 0 solid #c0c0c0 !important;
    border-left: 16px solid transparent !important;
  }

  .timeline-horizontal .timeline-item:before,
  .timeline-horizontal .timeline-item:after {
    display: none;
  }

  .timeline-horizontal .timeline-item .timeline-badge {
    top: auto;
    bottom: 0px;
    left: 43px;
  }
</style>



